.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.cluster-marker {
  color: #fff;
  background: #1978c8;
  border-radius: 50%;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.position-marker {
  background: none;
  border: none;
}
.position-marker div.detail-popup {
  display: none;
}
.position-marker div.detail-popup div.name {
  font-size: 1.3em;
  font-weight: 600;
}
.position-marker div.detail-popup div.address {
  margin-top: 10px;
  margin-bottom: 10px;
}

.position-marker:hover div.detail-popup {
  display: block;
  padding: 10px;
  min-width: 200px;
  background: #c4e2ff;
  min-height: 30px;
  color: #00366a;
  position: relative;
  z-index: 5;
  box-shadow: 0px 2px 7px -1px rgba(89, 89, 89, 0.69);
}

.position-marker div.detail-popup div {
  text-align: start;
}

.position-marker img {
  width: 25px;
}
.card-cover {
  background: #282c34;
  height: 100%;
}

.header-bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  min-height: 30%;
  width: 100%;
  background: #0b639e;
}
.header-bar div:first-child {
  font-weight: 600;
  color: white;
  font-size: 3.5vh;
  margin-top: 20px;
  height: fit-content;
}

.map-card {
  max-width: 1000px;
  margin: auto;
  height: 80%;
  margin-top: -10vh;
}

.search-wraper {
  text-align: center;
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 20px;
}

.search-wraper input {
  max-width: 519px;
  padding: 10px;
  width: 100%;
  height: 37px;
  box-sizing: border-box;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
